      leftCurrentPage: 1,
      rightCurrentPage: 1
    };
  },
  methods: {
    // 添加模板
    onAddFormwork() {
      this.dialogFormwork = true;
    },
    // 选择模板类型
    onFormworkType(val) {
      if (val == "collect") {
        this.isvisible_permission = true;
      } else {
        this.isvisible_permission = false;
      }
    },
    // 模板列表每页条数
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    // 模板列表页数
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    // 搜索模板商品
    onSearchGoods() {
      this.leftGoodList = this.leftGoodList.filter(
        data =>
          !this.searchGoods ||
          data.trade_name.toLowerCase().includes(this.searchGoods.toLowerCase())
      );
      this.rightGoodList = this.rightGoodList.filter(
        data =>
          !this.searchGoods ||
          data.trade_name.toLowerCase().includes(this.searchGoods.toLowerCase())
      );
    },
    // 将左边表格选择项存入虚拟列表中
    onLeftSelection(rows) {
      this.leftVirtuallist = [];
      rows.forEach(row => this.leftVirtuallist.push(row));
    },
    // 左边表格选择项移到右边
    onAddGood() {
      setTimeout(() => {
        this.$refs["staffTable"].clearSelection();
        this.$refs["selectedStaffTable"].clearSelection();
      }, 0);
      this.leftVirtuallist.forEach(item => this.rightGoodList.push(item));
      this.leftGoodList.map((obj, i) => {
        this.leftVirtuallist.map((obj1, j) => {
          if (this.leftGoodList[i].id === this.leftVirtuallist[j].id) {
            this.leftGoodList.splice(i, 1);
          }
        });
      });
    },
    // 右边表格选择项移到左边
    onRemovekGood() {
      setTimeout(() => {
        this.$refs["staffTable"].clearSelection();
        this.$refs["selectedStaffTable"].clearSelection();
      }, 0);
      this.rightVirtuallist.forEach(item => this.leftGoodList.push(item));
      this.rightGoodList.map((obj, i) => {
        this.rightVirtuallist.map((obj1, j) => {
          if (this.rightGoodList[i].id === this.rightVirtuallist[j].id) {
            this.rightGoodList.splice(i, 1);
          }
        });
      });
    },
    // 将右边表格选择项存入s虚拟列表中
    onRightSelection(rows) {
      this.rightVirtuallist = [];
      rows.forEach(row => this.rightVirtuallist.push(row));
    },
    // 模板商品左边页数
    leftHandleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    // 模板商品右边页数
    rightHandleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    onSaveFormworkInfo(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
